import './app.scss';

// import { TimelineMax } from "gsap/TimelineMax";

const tl = new TimelineMax();

const loadScreen = document.querySelector('.js-load-screen');
const irixLogoWhite = document.querySelector('.js-irix-white');
const sOneGlitters = document.querySelector('.js-s1-bg');
const sOneBox = document.querySelector('.js-s1-box');

const container = document.querySelector('.container');

window.addEventListener('load', _ => {
    tl
    .to(loadScreen, 1, { opacity: 0, onComplete: _ => loadScreen.remove() })
    .from(irixLogoWhite, 0.8, { y: 20, opacity: 0 })
    .from(sOneGlitters, 0.8, { y: 50, opacity: 0 })
    .from(sOneBox, 0.8, { y: 20, opacity: 0 })

    const controller = new ScrollMagic.Controller();

    const tl2 = new TimelineMax();

    tl2
        .staggerFrom(document.querySelectorAll('.js-bg'), 0.5, { y: 40, opacity: 0 }, 0.2)
        .staggerFrom(document.querySelectorAll('.js-work'), 0.5, { y: 40, opacity: 0 }, 0.2)

    new ScrollMagic.Scene({
        triggerElement: '.js-arrow',
        triggerHook: 0
    })
    .setTween(tl2)
    .addTo(controller)

    
})

